import Vue from 'vue'
import App from './App.vue'
import router from './router/index.js'

import store from './store/index.js' // vuex

import 'css/index.scss' // css

import 'lib-flexible/flexible' // flexible

import VueLazyload from 'vue-lazyload'

Vue.config.productionTip = false // 

// vue-devtools
Vue.config.devtools = process.env.NODE_ENV === 'development'

Vue.use(VueLazyload,{
    preLoad:1,
    error:require('../img/error.jpg'), // requirevue-lazyloadmain.jswebpacksrcwebpackimgvue-lazyload
    loading:require('../img/imgLoading.gif'),
    attempt:1
})

new Vue({
    el:'#app',
    router,
    store, //  store  store  this.$store 
    components:{
        App
    },
    template:"<App />"
})



